import React from 'react';
import {Switch, Input} from 'antd';
import Star from '../icon/Star';
import {pdf} from '../../common/images/account';
import {close} from '../../common/images/partners';
import './index.scss';

export const File = ({title, size}) =>
  <div className='file'>
    <img src={pdf} alt='pdf'/>
    <div className='file__info'>
      <h5 className='file__info-title'>{title}</h5>
      <div className='file__info-size'> : {size}</div>
    </div>
  </div>

export const SelectItem = ({option, dropSelectFilter}) =>
  <div className='select-item'>
    <div className='select-item__text'>
      {option.isSearch ? option.search : option}
    </div>
    <div className='select-item__drop' onClick={() => dropSelectFilter(option, option.isSearch && option.search)}>
      <img src={close} alt='close'/>
    </div>
  </div>

const photos = ['https://support.discord.com/system/photos/3604/7189/1272/profile_image_394014803452_678183.jpg','https://forum.xda-developers.com/data/avatars/o/4724/4724592.jpg?1600603563','https://support.discord.com/system/photos/3604/7189/1272/profile_image_394014803452_678183.jpg'];

export const CaseInfo = ({text, files, title}) => {
  console.log(photos)
  return <>
    <div className='case-text'>
      <p>{text}</p>
    </div>
    <div className='case-files'>
      <div className='case-files__title'>{title}</div>
      <div className='case-files__main'>
        {files.map(({title, size}, i) => <File key={title + i} title={title} size={size}/>)}
      </div>
    </div>
    <div className='case-files'>
      <div className='case-files__title'></div>
      <div className='case-files__main photo-files'>
        {photos.map((img, i) => <img className='photo-file' key={img + i} src={img} alt=' '/>)}
      </div>
    </div>
  </>
}
export const TextArea = ({text, setText}) =>
  <div className='textarea'>
    <div className='textarea__title'> </div>
    <Input.TextArea placeholder=' ' value={text} onInput={e => setText(e.target.value)}/>
    <div className='textarea__words'>  {text.length} / 5000</div>
  </div>

export const Stars = ({stars}) => {
  return Array(7).fill(0).map((_, i) => i < stars ? <Star key={i} fill='#feda00'/> : <Star key={i}/>)
}
export const SearchAccess = () =>
  <>
    <div className='search-access'>
      <div className='search-access__title'>  </div>
      <Switch/>
    </div>
    <div className='search-access__text'>
         ,
              ,
                .
    </div>
  </>
